html {
  font-size: 17px;
}

@media (max-width: 1200px) {
  html {
    font-size: 13px;
  }
}

@media (max-width: 900px) {
  html {
    font-size: 15px;
  }
}

@media (max-width: 400px) {
  html {
    font-size: 13px;
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif !important;
  overflow-x: hidden;
}

section {
  margin: 0% 5%;
  padding: 5% 0% 0 0;
}

h1,h2,p{
  cursor: default;
}

#preloader-div {
  position: fixed;
  top: 0%;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(256, 256, 256, 0.9);
  transition: opacity ease 0.5s;
  z-index: 1000;
}

#preloader {
  position: absolute;
  height: 15%;
  width: 15%;
  display: block;
}

#preloader #outer path {
  fill: #10e0ae;
}

#preloader #inner {
  transform-origin: center;
  -webkit-animation: rotateLeft 1s infinite alternate;
          animation: rotateLeft 1s infinite alternate;
}

#preloader #outer {
  transform-origin: center;
  -webkit-animation: rotateRight 1s infinite alternate;
          animation: rotateRight 1s infinite alternate;
}

.page-loaded {
  opacity: 0;
  pointer-events: none;
}

/* Sq anim */
#hero-bg {
  position: absolute;
  height: 80vh;
  display: block;
  z-index: 0;
  width: 20vw;
}

#hero-bg #smSqr {
  transform-box: fill-box;
  transform-origin: center;
  -webkit-animation: rotateLeft 5s infinite alternate;
          animation: rotateLeft 5s infinite alternate;
}

#hero-bg #bgSqr {
  transform-box: fill-box;
  transform-origin: center;
  -webkit-animation: rotateRight 5s infinite alternate;
          animation: rotateRight 5s infinite alternate;
}

.show-content {
  z-index: 10;
}

.navbar {
  z-index: 100;
}

@-webkit-keyframes rotateRight {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotateRight {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes rotateLeft {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

@keyframes rotateLeft {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

@-webkit-keyframes zoom {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}

@keyframes zoom {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}

@-webkit-keyframes shakeIcon{
  0%{
    transform: rotate(0deg);
  }
  25%{
    transform: rotate(-13deg);
  }
  50%{
    transform: rotate(13deg);
  }
  100%{
    transform: rotate(0deg);
  }
}

@keyframes shakeIcon{
  0%{
    transform: rotate(0deg);
  }
  25%{
    transform: rotate(-13deg);
  }
  50%{
    transform: rotate(13deg);
  }
  100%{
    transform: rotate(0deg);
  }
}

.hamburger-menu {
  background-color: #2aeaff;
  padding: 5px;
  border-radius: 10%;
}

.header {
  min-height: 100vh;
  /* width: 100vw; */
  background: radial-gradient(#5085f7, #052e88);
  /* width: 100vw; */
  background: url(/web-agent/static/media/bg_wave.873c6b4e.svg) no-repeat;
  background-size: cover;
}

.header .navbar {
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 10vh;
  position: fixed;
  width: 100%;
}

.header .navbar .logo {
  font-size: 2.4rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #fff;
}

.header .navbar .nav-links {
  display: flex;
  transition: 0.3s ease all;
}

.header .navbar .nav-links li {
  list-style-type: none;
}

.header .navbar .nav-links li a {
  text-decoration: none;
  color: #fff;
  font-size: 1.05rem;
  margin: 0 1.2rem;
  position: relative;
  cursor: pointer;
}

.header .navbar .nav-links li a::after {
  position: absolute;
  content: "";
  bottom: -4px;
  left: 0;
  height: 2px;
  width: 0%;
  background-color: #fff;
  display: block;
}

.header .navbar .nav-links li a:hover::after {
  width: 100%;
  transition: 0.3s ease all;
}
.header .navbar .nav-links .active::after{
  width: 100%;
  transition: 0.3s ease all;
}

.header .navbar .nav-links.nav-active {
  transform: translateX(0%);
}

.header .hamburger-menu {
  cursor: pointer;
  display: none;
}

.header .hamburger-menu div {
  width: 30px;
  height: 4px;
  background-color: #fff;
  margin: 6px;
  border-radius: 10px;
  transition: 0.3s ease all;
}

.header .navbar.sticky {
  background: #1799e2;

  transition: 0.3s ease all;
}

.header .navbar.sticky .logo {
  padding-left: 0.8rem;
  transition: 0.3s ease all;
}

.header .navbar.sticky .nav-links {
  padding-right: 0.8rem;
  transition: 0.3s ease all;
}

.header .navbar.sticky {
  padding-right: 1rem;
  transition: 0.3s ease all;
}

.header .showcase {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 2rem;
}

.header .showcase h1 {
  font-size: 2.4rem;
  word-spacing: 3px;
  color: #eee;
}

.header .showcase button {
  margin-top: 3rem;
  border: none;
  outline: none;
  background-color: transparent;
  border: 2px solid #eee;
  padding: 0.8rem 2rem;
  font-size: 1rem;
  font-family: inherit;
  color: #fff;
  border-radius: 3px;
  cursor: pointer;
}

.header .showcase button a {
  text-decoration: none;
  color: #fff;
}

.header .showcase button:hover a {
  text-decoration: none;
  color: #000;
}

.header .showcase button:hover {
  background-color: #eee;
  color: #052e88;
  transition: 0.3s ease all;
}

.clients {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.sec-title {
  font-size: 2rem;
  color: #e21757;
}

.clients-container,
.services-container{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 2% 2%;
}
.works-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 5% 0;
}

.clients-container .client {
  flex-basis: 21%;
}

.clients-container .client img {
  padding-top: 2%;
  height: 200px;
  width: 200px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  transition: all 0.2s ease;
}

.clients-container .client img:hover {
  -webkit-filter: none;
          filter: none;
  transform: scale(1.2);
}

#services,#portfolio,#about,#contact {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.service {
  border-radius: 5%;
  margin: 2%;
  padding: 2%;
  min-width: 200px;
  flex-basis: 25%;
  background-color: rgb(244 244 244);
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.08);
  transition: transform 0.5s ease !important;
}

.service h2 {
  text-transform: uppercase;
  padding-bottom: 5%;
}

.service p {
  text-align: justify;
}

.service .icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  top: 0;
  height: 80px;
  width: 80px;
  border: 2px solid #1799e2;
  border-radius: 50%;
  margin-bottom: 5%;
}

.service .icon svg {
  height: 45px;
  width: 45px;
  transition: all 0.3s ease !important;
  /* border: 2px solid rgb(0, 132, 255);
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 1); */
}

.service .icon img {
  border: 2px solid rgb(0, 132, 255);
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 1);
}

.sec-desc{
  font-size: 1.2em;
}

.service:hover{
  transform: scale(1.1)!important;
}

.service:hover .icon{
  background-color: rgb(255, 255, 255);
}

.service:hover svg{
  -webkit-animation: shakeIcon 0.4s ease;
          animation: shakeIcon 0.4s ease;
}

.work{
  height: 200px;
  width: 200px;
  flex-basis: 20%;
  /* display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; */
  position: relative;
  margin: 1%;
  cursor: pointer;
}

.work .details{
  position: absolute;
  top:50%;
  bottom: 50%;
  margin: auto;
  display: flex;
  /* background-color: rgba(23, 153, 226,0.8); */
  background-color: rgba(226, 23, 87,0.85);
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
  opacity: 0;
  /* cursor: pointer; */
  transition: all 1s ease;
}

.work .details h3{
  text-transform: uppercase;
}

.work img{
  height: 200px;
  width: 200px;
}

.work:hover .details{
  top: 0;
  bottom: 0;
  opacity: 1;
}
.about-container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 5% 0;
}
.about-container .text-content{
  text-align: left;
  width: 80%;
  font-size: 1.1rem;
  line-height: 2rem;
}
.about-container .img{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.about-container .img img{
  height: 280px;
  border-radius: 5%;
  margin: 5% 0;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.08);
  flex-basis: 40%;
}
.about-container .img .text-content-list{
  flex-basis: 45%;
  text-align: left;
}
.about-container .img .text-content-list ul{
  list-style-type: none !important;
  font-size: 1rem;
  margin: 0 5%;
}
.about-container .text-content-list ul li{
  list-style-type: none !important;
  margin: 1.1rem;
}
.about-container .text-content-list .fas{
  color:rgba(226, 23, 87,0.85);
  margin-right: 0.5rem;
}
.contact-container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}
.contact-container .form,
.contact-container .info{
  flex-basis: 40%;
  margin: 5%;
  min-width: 300px;
}
.footer{
  height: 40vh;
  background-image: url(/web-agent/static/media/footer_v2.6db58a54.svg);
  background-size:cover;
  background-repeat: no-repeat;
  position: relative;
}
.footer-container{
  position: absolute;
  top: 40%;
  margin: 0 5%;
  color: #fff;
  width: 90%;
}
.footer-content{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.company{
  flex-basis: 40%;
}
.social{
  cursor: pointer;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.social > *{
  flex-basis: 50%;
}
.credits{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 2%;
}
.credits p:last-child{
  font-size: 0.7rem;
}
/* Small devices */
@media (max-width: 725px) {
  section {
    margin: 0 2%;
    padding-top: 20%;
  }
  .work{
    margin: 5%;
  }
  .work,.work img{
    height: 250px;
    width: 250px;
  }

  .clients-container {
    margin: 0;
  }

  .clients-container .client img {
    height: 150px;
    width: 150px;
  }

  .service {
    margin: 10% 0%;
    padding: 10%;
    min-width: 300px;
  }

  #hero-bg {
    position: absolute;
    height: 80vh;
    display: block;
    z-index: 0;
    width: 50vw;
  }
  .about-container .img img{
    height: 200px;
    border-radius: 5%;
    margin: 5% 0;
    flex-basis: 40%;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.08);
  }
  .about-container .text-content{
    text-align: justify;
    width: 90%;
    font-size: 1.1rem;
    line-height: 2rem;
  }
  .about-container .text-content-list{
    min-width: 100%;
  }
  .about-container .text-content-list ul{
    list-style-type: none;
    font-size: 1rem;
    margin: 0 1%;
  }

  .credits{
    margin-top: 8%;
  }
  .social > *{
    flex-basis: 30%;
  }

  .navbar .nav-links {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 225px;
    background: #1799e2;
    margin: 0;
    padding: 0;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    transform: translateX(-100%);
    z-index: 10;
  }

  .navbar .hamburger-menu {
    display: block;
  }

  .navbar .bar-change .bar1 {
    transform: rotate(-45deg) translate(-9px, 6px);
  }

  .navbar .bar-change .bar2 {
    opacity: 0;
  }

  .navbar .bar-change .bar3 {
    transform: rotate(45deg) translate(-9px, -5px);
  }
}
